// 使用你的配置
const SUPABASE_URL = 'https://rktotbfhdvvmazabyvme.supabase.co'
const SUPABASE_ANON_KEY = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InJrdG90YmZoZHZ2bWF6YWJ5dm1lIiwicm9sZSI6ImFub24iLCJpYXQiOjE3NjA1OTgyMjksImV4cCI6MjA3NjE3NDIyOX0.bTUxD6-1S2vM3zyC2uhqf4hfln97rH5fqJbptbSZIVQ'

// 创建 Supabase 客户端
export const supabase = createClient(SUPABASE_URL, SUPABASE_ANON_KEY)

// 数据同步服务
export class SyncService {
  static async saveData(userId, key, data) {
    try {
      const storageKey = `study-tracker-${userId}-${key}`
      
      console.log('🔄 正在保存数据到 Supabase:', { userId, key, storageKey })
      
      // 1. 保存到 localStorage
      localStorage.setItem(storageKey, JSON.stringify(data))
      
      // 2. 同步到 Supabase - 使用 upsert 而不是 insert
      const { error } = await supabase
        .from('user_data')
        .upsert({
          user_id: userId,
          data_key: storageKey,
          data_value: data,
          updated_at: new Date().toISOString()
        }, {
          onConflict: 'user_id, data_key'  // 指定冲突解决字段
        })
      
      if (error) {
        console.warn('⚠️ Supabase 保存失败，使用本地存储:', error.message)
        return false
      } else {
        console.log('✅ Supabase 同步成功:', key)
        return true
      }
      
    } catch (error) {
      console.error('❌ 保存数据失败:', error)
      return false
    }
  }

  static async loadData(userId, key) {
    try {
      const storageKey = `study-tracker-${userId}-${key}`
      
      console.log('🔄 正在从 Supabase 加载数据:', { userId, key, storageKey })
      
      // 1. 先尝试从 localStorage 加载
      const localData = localStorage.getItem(storageKey)
      if (localData) {
        console.log('✅ 从本地存储加载数据:', key)
        return JSON.parse(localData)
      }
      
      // 2. 如果本地没有，从 Supabase 加载
      const { data: supabaseData, error } = await supabase
        .from('user_data')
        .select('data_value')
        .eq('user_id', userId)
        .eq('data_key', storageKey)
        .single()
      
      if (error) {
        console.log('ℹ️ Supabase 无数据或加载失败:', key, error.message)
        return null
      }
      
      if (!supabaseData) {
        console.log('ℹ️ Supabase 无数据:', key)
        return null
      }
      
      // 保存到 localStorage 备用
      localStorage.setItem(storageKey, JSON.stringify(supabaseData.data_value))
      console.log('✅ 从 Supabase 加载成功:', key)
      return supabaseData.data_value
      
    } catch (error) {
      console.error('❌ 加载数据失败:', error)
      return null
    }
  }
}